.row{
    padding-left: 40px;
    padding-bottom: 10px;
}
.row >h1{
    padding: 20px;
}
.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    cursor: pointer;
}
.row__posters::-webkit-scrollbar{
    display: none;
}
.row__poster{
    object-fit: contain;
    width: 100%;
    max-height: 150px;
    transition: transform 450ms;
    margin-right: 1px;
    padding: 5px;
}
.row__posterLarge{
    max-height: 348px
}

.row__poster:hover{
    transform: scale(1.08);
}