.nav{
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 10px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 450ms;
}
.nav__logo{
    width: 100px;
}
.nav__avatar{
    width: 40px;
    margin-right: 20px;
}
.nav__black{
    background-color: #000;
}