.banner{
    color: white;
    object-fit: contain;
    height: 448px;
    width: 100%;
    margin-bottom: 20px;
}
.banner__button{
    padding-left: 10px;
}
.banner__button > button{
    background-color:  rgba(51, 51, 51, 0.5);
    color: #fff;
    padding: 10px;
    width: 100px;
    border: none;
    outline: none;
    margin-left: 10px;
    cursor: pointer;
}
.banner__button > button:hover{
    background-color: #fff;
    color: #000;
    transition: all 0.2s;
}
.banner__title{
    padding-top: 140px;
    padding-left: 20px;
    padding-bottom: 10PX;
    font-size: 3rem;
    font-weight: 800;
}
.banner__description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    padding-left: 20px;
    max-width: 360px;
    height: 80px;
}
.foot__bar{
    height: 7.8rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,0.6),
        #111
    )
}